.popup {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: #26404d;
  border: #a15429 solid 2px;
  color: #a15429;
  font-size: 2rem;
  height: 100vh;
  padding: 20px;
  position: absolute;
  width: 100vw;
  z-index: 2; }
  .popup h1 {
    color: #a15429;
    font-family: "Nova Flat", sans-serif;
    font-size: 5.4rem;
    margin: 0;
    opacity: 1; }
  .popup button {
    width: 230px;
    height: 80px;
    font-size: 1.6rem; }

.App {
  background-image: url(/compare_a_country/static/media/background.f077c7eb.jpg);
  background-size: cover;
  font-family: "Niramit", sans-serif; }

.App-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
