  transition: opacity 0.6s;
  width: 110px; }
  nav li:hover {
    cursor: pointer;
    opacity: 0.1; }

.filter-class {
  padding-left: 20px;
  padding-right: 30px;
  position: absolute;
  background-color: red;
  z-index: 1;
  top: 118px;
  background-color: #26404d;
  right: 0;
  width: 40%;
  display: flex;
  height: 100vh;
  width: 36vw;
  overflow-y: scroll;
  display: flex;
  flex-direction: column; }
  .filter-class form {
    margin: 0px 20px 50px 0; }
  .filter-class h3 {
